:root {
  --dropDownWidth: 180px;
  --dropDownYOffset: 125px;
  --dropDownXOffset: 10px;
}

.furniture-dropdowns {
  display: flex;
  /* justify-content: space-evenly;
  width: 650px; */
  margin-bottom: var(--dropDownYOffset);
  z-index: 20;
}

.furniture-dropdowns select {
  width: var(--dropDownWidth);
  font-size: 20px;
}

.furniture-dropdowns select {
  margin-left: calc(var(--dropDownXOffset) / 2);
  margin-right: calc(var(--dropDownXOffset) / 2);
}

@media screen and (max-width: 480px) {
  :root {
    --dropDownWidth: 100px;
    --dropDownYOffset: 50px;
    --dropDownXOffset: 20px;
  }
}
