:root {
  --center: -10%;
  --centerSides: 10%;
  --sideTop: 20%;
  --sideChairs: -10%;
  --tableCenter: 5%;

  --tableWidth: 1500px;
  --tableHeight: 215px;

  --holderWidth: 400px;
  --holderHeight: 153px;

  --sideIndex: 1;
  --centerIndex: 0;

  --backgroundWidth: 650px;
  --backgroundHeight: 400px;

  --chairSize: 125px;
}

.backgrounds {
  position: absolute;
}

.backgrounds img {
  width: var(--backgroundWidth);
  height: var(--backgroundHeight);
}

.chairs {
  position: absolute;
}

.chairs img {
  width: var(--chairSize);
  height: var(--chairSize);
}

.left-side {
  top: var(--sideTop);
  left: var(--sideChairs);
  z-index: var(--sideIndex);
}

.right-side {
  top: var(--sideTop);
  right: var(--sideChairs);
  z-index: var(--sideIndex);
}

.center {
  top: var(--center);
  z-index: var(--centerIndex);
}

.left {
  left: var(--centerSides);
}

.right {
  right: var(--centerSides);
}

.tables {
  width: calc(var(--tableWidth));
  height: calc(var(--tableHeight));
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  top: var(--tableCenter);
}

.holder {
  width: var(--holderWidth);
  height: var(--holderHeight);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
}

@media screen and (max-width: 480px) {
  :root {
    --center: -5%;
    --centerSides: 20%;
    --sideTop: 20%;
    --sideChairs: -5%;
    --tableCenter: 5%;

    --tableWidth: 1250px;
    --tableHeight: 178px;

    --holderWidth: 350px;
    --holderHeight: 133px;

    --backgroundWidth: 450px;
    --backgroundHeight: 276px;

    --chairSize: 100px;
  }
}
